.products__grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 168px;
  padding-bottom: 60px;
  padding-left: 20px;
  padding-right: 20px;
}

.products {
  &__btn-service {
    border: none;
    background-color: transparent;
    padding: 0;
    cursor: pointer;
    margin-top: auto;
    margin-bottom: 16px;
  }
  &__label {
    color: #131313;
    text-align: center;
    font-size: 18px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 800;
    line-height: normal;
    text-transform: uppercase;
    padding-top: 60px;
  }
  &__title {
    color: #131313;
    text-align: center;
    font-size: 32px;
    font-family: Seymour One;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 1.28px;
    text-transform: uppercase;
    margin-bottom: 146px;
  }
  &__title-card {
    color: #131313;
    text-align: center;
    font-size: 20px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    margin-bottom: 40px;
    margin-top: 123px;
  }
  &__text {
    color: #131313;
    text-align: center;
    font-size: 14px;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 400;
    line-height: 176%;
    letter-spacing: 0.56px;
    padding: 0 27px;
  }
}
.products__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 24px;
  height: 420px;
  position: relative;

  &--color-green {
    background-color: #aec6a5;
  }
  &--color-yellow {
    background-color: #efd478;
  }
  &--color-orange {
    background-color: #d6936d;
  }
  &--color-green::after {
    content: '';
    position: absolute;
    top: -97px;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    background: linear-gradient(0deg, rgba(156, 190, 143, 0.7) 0%, rgba(156, 190, 143, 0.7) 100%),
      #d9d9d9;
    box-shadow: 0px 5.342193126678467px 20.033222198486328px 0px rgba(10, 136, 23, 0.25);
    z-index: 1;
  }

  &--color-yellow::after {
    content: '';
    position: absolute;
    top: -97px;
    width: 200px;
    height: 200px;
    border-radius: 50%;

    background: linear-gradient(0deg, rgba(248, 210, 78, 0.7) 0%, rgba(248, 210, 78, 0.7) 100%),
      #d9d9d9;
    box-shadow: 0px 5.403973579406738px 20.26490020751953px 0px rgba(153, 114, 23, 0.25);
    z-index: 1;
  }
  &--color-orange::after {
    content: '';
    position: absolute;
    top: -97px;
    width: 200px;
    height: 200px;
    border-radius: 50%;

    background: linear-gradient(0deg, rgba(213, 117, 63, 0.7) 0%, rgba(213, 117, 63, 0.7) 100%),
      #d9d9d9;
    box-shadow: 0px 5.413332939147949px 20.299999237060547px 0px rgba(0, 0, 0, 0.25);
    z-index: 1;
  }
}
.products__img {
  position: absolute;
  top: -108px;
  left: 50%;

  transform: translateX(-50%);
  z-index: 2;
}

@media (min-width: 480px) {
  .products__grid {
    padding-left: 60px;
    padding-right: 60px;
  }
}
@media (min-width: 768px) {
  .products__grid {
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    padding-left: 19px;
    padding-right: 19px;
  }
}

@media (min-width: 1200px) {
  .products__grid {
    gap: 29px;
    padding-left: 25px;
    padding-right: 25px;
  }
  li.products__list {
    height: 536px;
  }
  h2.products__title {
    margin-bottom: 300px;
  }
}
@media (min-width: 1200px) {
  .products__img {
    top: -183px;
  }
  .products__title-card {
    margin-top: 160px;
  }
  .products__list {
    &--color-green::after {
      content: '';
      position: absolute;
      top: -172px;
      width: 302px;
      height: 302px;
    }

    &--color-yellow::after {
      content: '';
      position: absolute;
      top: -172px;
      width: 302px;
      height: 302px;
    }

    &--color-orange::after {
      content: '';
      position: absolute;
      top: -172px;
      width: 302px;
      height: 302px;
    }
  }
  .products__btn-service {
    margin-bottom: 60px;
  }
}

.background {
  position: relative;
}

.background::before {
  content: '';
  position: absolute;
  background-image: url(/src/images/mobile/background_mobile.png);
  background-size: cover;
  background-position: center;
  background-repeat: repeat;
  z-index: -1;
  width: 100%;
  height: 100%;
}
@media (min-width: 768px) {
  .background::before {
    content: '';
    position: absolute;
    background-image: url(/src/images/tablet/background-tablet.png);
    background-size: cover;
    background-position: center;
    background-repeat: repeat;
    z-index: -1;
    width: 100%;
    height: 100%;
  }

  @media (min-width: 1200px) {
    .background::before {
      content: '';
      position: absolute;
      background-image: url(/src/images/desktop/background-desktop.png);
      background-size: cover;
      background-position: center;
      background-repeat: repeat;
      z-index: -1;
      width: 100%;
      height: 100%;
    }
  }
}
