.hero {
  background-color: rgba(132, 161, 120, 1);

  &__container {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    padding: 0 20px;
    background-image: url(/src/images/mobile/x2/dotsMobile@2x.png);

    background-repeat: no-repeat;
    background-position: bottom;
    background-position-x: right 0px;
    background-size: contain;

    @include minimobile {
      min-width: 177px;
      //max-width: 320px;
      height: 502px;
    }

    @include mobile {
      min-width: 320px;
      min-height: 415px;
      max-width: 480px;
      height: 502px;
      background-position-x: right -50px;
    }

    @include tablet {
      min-height: 415px;
      padding: 0 32px;
      max-width: 100%;
      background-image: url(/src/images/tablet/dotsTablet.png);
    }

    @include desktop {
      max-width: 1200px;
      min-height: 664px;
      
      padding: 0 16px;
      background-image: url(/src/images/desktop/x2/dotsDesktop@2x.png);
    }
  }

  &__content-wrap {
    position: relative;
    justify-content: space-between;
    align-items: flex-end;
    display: flex;
  }

  &__main-content {
    max-width: 300px;
    padding: 44px 0 0 20px;

    @include tablet {
      max-width: 250px;
      padding: 14px 32px 0px 32px;
    }

    @include desktop {
      max-width: 400px;
      padding: 16px 16px 0px 16px;
    }
  }

  &__title {
    height: 97px;
    color: $title-white;
    font-family: Seymour One;
    font-size: 26px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 1.04px;
    text-transform: uppercase;
    margin-bottom: 28px;

    @include tablet {
      margin-top: 0;
      width: 247px;
    }

    @include desktop {
      width: 367px;
      font-size: 38px;
      letter-spacing: 1.52px;
      margin-bottom: 32px;
    }
  }

  &__main-list {
    display: flex;
    align-items: baseline;
    gap: 12px;
    margin-top: 32px;
    min-width: 110px;

    @include tablet {
      margin-top: 1.5em;
      margin-bottom: 0.75em;
      width: 274px;
    }

    @include desktop {
      margin-top: 80px;
      margin-bottom: 3.625em;
    }
  }

  &__main-linkA {
    color: $black;
    padding: 11px 26px;

    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;
    border-radius: 8px;
    border: 1px solid #f8d24e;

    &:hover {
      border-color: #0a8817;
    }
    &:focus {
      border-color: $title-white;
    }
    &:disabled {
      border-color: #ababab;
    }
  }

  &__main-linkB {
    color: #f8d24e;

    //padding: 11px 26px;

    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 18px;

    text-decoration: underline;
    &:hover {
      color: #0a8817;
    }
    &:focus {
      color: #0a8817;
    }
    &:disabled {
      color: #ababab;
    }
  }

  &__buttons {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }

  &__button {
    color: #000;
    text-align: center;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding: 11px 26px;
    border-radius: 8px;
    border: 2px solid #f8d24e;

    @include desktop {
      min-width: 134px;
      height: 42px;
      padding: 12px 34px;
      border-radius: 10px;
    }

    &:not(:last-child) {
      margin-right: 8px;
      @include desktop {
        margin-right: 12px;
      }
    }

    &:nth-child(even) {
      color: #f8d24e;
      text-align: center;
      font-family: Montserrat;
      font-size: 12px;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      text-decoration-line: underline;
      border: none;
      &:focus {
        color: #fcfcfc;
      }
      &:hover {
        color: #0a8817;
      }
      &:disabled {
        color: #ababab;
      }
    }
  }

  &__amount {
    display: none;

    @include tablet {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  &__txt {
    color: #131313;
    text-align: right;
    font-family: Montserrat;
    font-size: 28px;
    font-style: normal;
    font-weight: 900;
    line-height: normal;
    letter-spacing: 1.12px;
    text-transform: uppercase;

    @include desktop {
      font-size: 46px;

      font-weight: 900;

      letter-spacing: 1.84px;
    }
  }

  &__txt-caffe {
    color: #fcfcfc;
    text-align: right;
    font-family: Montserrat;
    font-size: 8px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @include tablet {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    @include desktop {
      font-size: 14px;

      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
  }

  &__wrap-plate {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    @include tablet {
      position: absolute;
      bottom: 3%;
      left: 5%;
      gap: 33px;
    }

    @include desktop {
      position: absolute;
      left: 2%;
      bottom: 8%;
      gap: 14px;
      z-index: 5;
    }
  }

  &__ad {
    display: none;

    @include tablet {
      max-width: 151px;
      display: block;
    }
    @include desktop {
      
      max-width: 200px;
    }
  }

  &__plate-img {
    display: none;

    @include tablet {
      display: block;
    }
  }

  &__after-title {
    @include tablet {
      margin-bottom: 32px;
      color: #fff;
      font-size: 12px;
      font-weight: 400;
      line-height: 140%;
      width: 165px;
    }

    @include desktop {
      margin-bottom: 32px;
      color: #fff;
      font-size: 12px;
      font-weight: 400;
      line-height: 140%;
    }
  }

  &__link-btn {
    color: #fff;
    display: block;
    background-color: transparent;
    border: none;
    margin: 0;

    &:hover > .hero__icon {
      width: 3.1875em;
    }

    &:focus > .hero__icon {
      width: 2.1669921875em;
      stroke-width: 1;
    }
  }

  &__icon {
    display: inline-block;
    width: 1em;
    height: 1em;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor;
    width: 2.5625em;
    transition-property: width;
  }

  /*&__image {
    position: relative;
    margin-left: 83px;
    z-index: 5;
    img {
      display: block;
      margin: 0;
      width: 90%;
    }
  }*/

  &__image {
    position: absolute;
    bottom: 0;
    width: 177px;
    //right: 20%;

    @include minimobile {
      width: 177px;
      right: 8%;
    }

    @include mobile {
      width: 218px;
      right: 15%;
      //right: 0%;
    }

    @include tablet {
      width: 268px;
      right: 9%;
    }

    @include desktop {
      width: 456px;
      right: 7%;
    }
  }
}
