* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1, h2, h3, h4, p {
  margin: 0;
}

body {
  width: 100%;
  background-color: #84a178;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  transform: scale;
  visibility: visible;
  z-index: 10;
  background-color: #0003;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  position: absolute;
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: calc(100vw - 40px);
    margin: 0 auto;
  }
}

@media screen and (min-width: 480px) {
